.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  transition: min-height linear 1s;
  min-height: 90vh;
  display: flex;
  justify-content: center;
  flex-direction: column;  
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header-nomain {
  color: white;
  min-height: 0;
}

.Div-Content{
  transition: background-color linear 1s;
  transition: min-height linear 1s;
  background-color:white;
  padding: 1%;
  color: #282c34;
  min-height: 70vh;
  font-size: calc(10px + 1vmin);
  border-radius:4px;
  text-align: initial;
}

.Div-Content-hide{
  background-color:#282c34;
  min-height:0px;
  overflow-y: hidden;
  overflow-x: hidden;
}

.Div-Content>h1{
  font-size: calc(10px + 2vmin);
}

.Div-Content>p{
  font-size: calc(10px + 1vmin);
}

.Div-Content>div{
  display: flex;
  padding:1%
  
}
.Div-Content>div>div>h2{
  font-size: calc(10px + 1vmin);
  padding:1%
}

.Div-Content>div>div>p{
  padding:1%
}

.Div-Content>div>img{
  height: 20%;
  width: 20%;
  max-height: 444px;
  vertical-align: middle;
}

.PaperDiv{
  display: flex;
  margin-top:2%

}
.PaperDiv>img{
  /* height: 10%; */
  width: 10%;
  
  
  vertical-align: middle;
}

.authors{
  text-transform: capitalize;
}

.App-link {
  color: #61dafb;
}

.Menu{
  color: white;
  display: inline;
  
}

.Menu > p {
  padding-left: 2.5%;
  display: inline;
}

.SelectMenu {
  text-decoration:underline;
}

.Paper {
  padding:1%
}